<template>
  <button
    type="button"
    :disabled="disabled"
    :class="['track-btn', [type || '']]"
    @click="handleClick"
  >
    <slot>
      {{ label }}
    </slot>
  </button>
</template>

<script>
export default {
  name: "Button",
  props: {
    label: {
      type: String,
      default: ""
    },
    handleClick: {
      type: Function,
      default: () => {}
    },
    disabled: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      validator: (value) =>
        ["round", "primary", "success"].includes(value.toLowerCase())
    }
  }
};
</script>

<style scoped lang="scss">
.track-btn {
  position: relative;
  display: inline-block;
  cursor: pointer;
  user-select: none;
  line-height: 1;
  touch-action: manipulation;
  border: 0.5px solid;
  text-align: center;
  white-space: nowrap;
  font-size: 12px;
  line-height: 14px;
  border-radius: 3px;
  padding: 5px 20px;
  box-sizing: border-box;
  background-color: #fff;
  color: #54ca7e;
  border-color: #54ca7e;
  font-weight: bold;
  text-transform: uppercase;
  &:hover {
    background-color: #54ca7e;
    color: #fff;
  }
  &:disabled {
    color: #a9a9a9;
    border-color: #a9a9a9;
    &:hover {
      color: #a9a9a9;
      background-color: #fff;
      border-color: #a9a9a9;
      cursor: not-allowed;
    }
  }
  &.round {
    border-width: 0.9px;
    border-radius: 60px;
    font-weight: 600;
    text-transform: none;
  }
}
</style>
