<template>
  <label class="checkbox-container" :aria-disabled="disabled" :class="classes">
    <!-- Customized label element -->
    <slot>
      <div class="checkbox-label">{{ label }}</div>
    </slot>
    <input
      ref="checkbox"
      type="checkbox"
      :checked="checked"
      :disabled="disabled"
      @change="updateChecked($event.target.checked)"
    />
    <!-- Customized icons -->
    <span v-if="iconChecked && iconUnchecked">
      <Icon class="icon-check" :name="iconChecked" v-if="checked" />
      <Icon class="icon-check" :name="iconUnchecked" v-else />
    </span>
    <!-- Default checkbox display -->
    <span
      v-else
      class="checkmark"
      :style="{ '--checkmark-color': checkmarkColor }"
      :class="[disabled ? 'disable' : '']"
    ></span>
    <!-- Error message -->
    <div class="checkbox-error q-mt-xs" v-if="error">{{ error }}</div>
  </label>
</template>
<script>
import Icon from "../Icon/Icon";
export default {
  name: "Checkbox",
  components: { Icon },
  props: {
    modelValue: [Boolean, Array],
    value: [String, Number],
    label: String,
    disabled: {
      type: Boolean,
      default: false
    },
    callback: {
      type: Function,
      default: () => {}
    },
    size: {
      type: String,
      default: "large",
      validator: function (value) {
        return ["small", "large"].indexOf(value) !== -1;
      }
    },
    rules: {
      type: Array,
      default: () => []
    },
    iconChecked: String,
    iconUnchecked: String,
    checkmarkColor: {
      type: String
    }
  },
  model: {
    prop: "modelValue",
    event: "input"
  },
  data() {
    return {
      checked: false,
      error: ""
    };
  },
  computed: {
    classes() {
      return {
        "checkbox-container": true,
        "checkbox-error": this.error,
        [`checkbox-size-${this.size}`]: true
      };
    }
  },
  watch: {
    modelValue(val) {
      if (Array.isArray(this.modelValue)) {
        this.checked = val.includes(this.value);
      } else {
        this.checked = val;
      }
    }
  },
  methods: {
    updateChecked: function (checked) {
      this.checked = checked;
      if (this.disabled) {
        return;
      }
      if (Array.isArray(this.modelValue)) {
        if (checked) {
          this.modelValue.push(this.value);
        } else {
          this.modelValue.splice(this.modelValue.indexOf(this.value), 1);
        }
      } else {
        this.$emit("input", checked);
      }
      this.callback(checked);
    },
    focus() {
      this.$refs.checkbox.focus();
    },
    validate() {
      for (let i = 0; i < this.rules.length; i += 1) {
        const rule = this.rules[i];
        const errorString = rule(this.checked);

        if (typeof errorString === "string") {
          this.error = errorString;

          return false;
        }
      }

      this.error = "";

      return true;
    }
  },
  mounted() {
    if (Array.isArray(this.modelValue)) {
      if (this.modelValue.includes(this.value)) {
        this.checked = true;
      }
    } else {
      if (this.modelValue) {
        this.checked = true;
      }
    }
  }
};
</script>
<style scoped lang="scss">
// @import "../../css/_tokens.scss";

.checkbox-container {
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #4f4f4f;
  font-size: 15px;
  display: flex;
}

.checkbox-label {
  font-size: 15px;
  display: flex;
  padding-left: 25px;
  display: flex;
  align-items: center;
}

.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  height: 20px;
  width: 20px;
  border: 1px solid #a2a2a2;
  border-radius: 4px;
  margin: auto;
}
.checkbox-error .checkmark {
  border-color: #ea575f;
}
.checkbox-size-small .checkmark {
  height: 16px;
  width: 16px;
}
.checkmark.disable {
  background: #eee;
  cursor: not-allowed;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.checkbox-container:hover input ~ .checkmark {
  background-color: #ddd;
}

.checkbox-container input:checked ~ .checkmark {
  border-color: var(--checkmark-color, var(--track-secondary));
}

.checkbox-container input:checked ~ .checkmark:after {
  display: block;
}

.checkbox-container .checkmark:after {
  left: 7px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid var(--checkmark-color, var(--track-secondary));
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.checkbox-size-small.checkbox-container .checkmark:after {
  left: 5px;
  top: 1px;
}

.checkbox-error {
  color: #ea575f;
}

.icon-check {
  position: absolute !important;
  top: -2px !important;
  left: 0;
  font-size: 20px;
}
</style>
